export default{
    evenOrOdd( state ){ //,
      state.conut % 2 === 0 ? '' : ''
    },
    count : state => state.count + 103,
    //todos.done true
    doneTodos: state => { //
      return state.todos.filter(item => item.done)
    },
    //state->,getters->
    doneTodesCount: (state,getters) => {
      return getters.doneTodos[1]
    },
    getTodoById:(state) => (id) => { // id
      return state.todos.find(todo => todo.id === id)
    }
}